$BackColor: #2B2B2B;
$HeaderColor: #191919;
$Font: 'Roboto', sans-serif;
$FontColor: #EFEFEF;
$Max-width:3000px;
$Max-height:1900px;
$Min-width:320px;
$Min-height:556px;

body {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: $BackColor ;
}

.container {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    min-width: $Min-width;
    min-height: $Min-height;
    max-width: $Max-width;
    max-height: $Max-height; 
}

.table {
    margin: 0 auto;
    padding: 0;
    width: 90%;
    height: auto;
    
   
}

.col-1 {
            width: 8.33333333333%;

        }

        .col-2 {
            width: 16.6666666667%;
        }
        .col-3 {
            width:25%;
        }
        .col-4 {
            width: 33.3333333333%;

        }
        .col-5 {
            width: 41.6666666667%;
        }
        .col-6 {
            width:50%;
        }
        .col-7 {
            width: 58.3333333333%;
        }
        .col-8 {
            width:66.6666666666%;
        }
        .col-9 {
            width:75%;
        }
        .col-10 {
            width: 83.3333333333%;
        }
        .col-11 {
             width:91.6666666666%; 
        }
        .col-12 {
             width: 100%;
        }

        [class*="col-"] {
          float:left; 
        }

        [class*="col-"]:last-of-type {
        }

        .row:before
            :after
        {
          clear: both;
          content:"";
        }

.menu{
	display: none;
	width:50px;
	height: 40px;
    padding:1em; 
    
    &:hover {
        cursor:pointer
    }
     li{
        list-style:none;
        margin:5px 0;
        width:100px;
        height:8px;
        background:#949494;
    }
    .on{
        transform:rotate(90deg);
    
        li{
            background:#ddd
        }
    }

    nav{
        padding:.2em 0;
        font:1em sans-serif;
        background-color: $BackColor;
        text-align: right;
        
        ul li{
            display:inline-block;
            margin:0 .8em;
            line-height: 3;
            text-align: center;
    }
        a{
            display:block;
            font-family: $Font;
            font-size: 100%;
            text-decoration: none;
            color: $FontColor;
            margin-left: 2%;
            
            &:hover {
             
                color: #999;
            }
         }
    }
    a, .menu, .menu li{

        transition: all 0.3s ease-out
    }

}
	
#logo-holder {
    
    margin: 0 auto;
    padding: 0;
    position: relative;
    top:  25%;
    left:0%;
    
    .logo {
        margin: 0;
        padding: 0;
        width: 100%;
    }
}

#text {
    margin: 0 auto;
    padding: 0;
    position: relative;
    text-align: center;
    padding-top: 15%;
    left:2%;
    
    h1{
        font-family: $Font;
        font-size: 150%;
        color: $FontColor;
    }
    
    p{
        font-family: $Font;
        font-size: 130%;
        color: $FontColor;
        text-align: justify;
        
    }
}
#links {
    margin: 0 auto;
    padding: 0;
    padding-top: 5%;
    text-align: right;
    
    
    .media-links {
        margin: 0 auto;
        padding: 0;
        width:2%;
        margin-left: 2%;
        
    }
}

